  margin: 30px;
}

.typingeffect {
  font-size: 3rem;
  font-size: var(--typing-font);
  margin-bottom: 30px;
}

/* .condiv class is for the right side content */
.condiv {
  padding: 100px;
  color: #353130;
  flex: 6 1;
  flex: var(--condiv-flex) 1;
}
/* .condiv class is for the right side content */

