}

@media all and (max-width: 450px) {
  :root {
    --fontsize-nav: 1rem;
    --sidebar-flex: 0;
    --typing-font: 2rem;
  }

  h3 {
    font-size: 1.5rem;
  }

  h4 {
    font-size: 1.2rem;
  }

  .App {
    flex-direction: column;
  }

  nav {
    padding: 30px;
  }

  .condiv {
    padding: 20px 40px;
    margin-top: 4rem;
  }
  
  nav ul {
    /* display: flex; */
  }

  nav ul li {
    margin: 10px;
  }
  .profilepic {
    margin: 0;
  }

  .typingeffect {
    font-size: 1.4rem;
  }

  .social {
    left: 0;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    bottom: 10px;
  }

  .social i {
    padding-right: 0;
  }

  .widecard {
    display: block;
  }

  .widecard .btn {
    margin: 0 auto;
  } 

  .workproj a {
    padding-left: 1rem;
  }

  .projectcard {
    background-size: auto auto;
    background-size: initial;
  }
  .contact h3 {
    font-size: 1.2rem;
  }


/* dropdown nav -----------------------------------------------------------------------*/
.nav li {
  /* float: left; */
}
.nav li a {
  padding: 20px 30px;
}
.nav .menu {
  clear: none;
  float: right;
  max-height: none;
}

.nav {
  background-color: #353130;
  box-shadow: 1px 1px 4px 0 rgba(0,0,0,.1);
  position: fixed;
  width: 100%;
  z-index: 3;
}

.nav ul {
  margin: 0 auto!important;
  padding: 0;
  list-style: none;
  overflow: hidden;
  /* background-color: #353130; */
}

.nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  overflow: hidden;
  /* background-color: #fff; */
}

.nav .menu {
  clear: both;
  max-height: 0;
  -webkit-transition: max-height .2s ease-out;
  transition: max-height .2s ease-out;
}

.nav .menu-icon {
  cursor: pointer;
  display: inline-block;
  float: right;
  padding-left: 20px;
  position: relative;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.nav .menu-icon .navicon {
  background: white;
  display: block;
  height: 3px;
  position: relative;
  -webkit-transition: background .2s ease-out;
  transition: background .2s ease-out;
  width: 22px;
}

.nav .menu-icon .navicon:before,
.nav .menu-icon .navicon:after {
  background: white;
  content: '';
  display: block;
  height: 100%;
  position: absolute;
  -webkit-transition: all .2s ease-out;
  transition: all .2s ease-out;
  width: 100%;

}

.nav .menu-icon .navicon:before {
  top: 6px;
}

.nav .menu-icon .navicon:after {
  top: -6px;
}

/* menu btn */

.nav .menu-btn:checked ~ .menu {
  max-height: 240px;
}

.nav .menu-btn:checked ~ .menu-icon .navicon {
  background: transparent;
}

.nav .menu-btn:checked ~ .menu-icon .navicon:before {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.nav .menu-btn:checked ~ .menu-icon .navicon:after {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.nav .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before,
.nav .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
  top: 0;
}
}


